    align-items: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
  }

  .header__container-wrapper--header__main {
    border-bottom: 1px solid $theme-gray-lighter;
    box-shadow: 0 4px 4px 0 rgba(#000, .2);
  }

  .header__main {
    padding: .5rem 0;
  }

  .header__social-menu {
    display: none;
    margin-right: .4rem;
    padding-right: .7rem;
    border-right: 1px solid $header-border-deviders;
    @include media-breakpoint-up(lg) {
      display: block;
    }
  }
}


// -----------
// Header D
// -----------
.header--header-d {
  .header__container-wrapper--header__top {
    background: $header-top-bg;
    border-bottom: $header-top-border-bottom;
  }

  .header__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
